import '../Assets/Styles/BlockchainNetwork.css';
import { useHistory,useParams } from 'react-router-dom';
import Editlistingform from './Editlistingform';
import { Modal, Button } from 'react-bootstrap';
import React, { useState,useEffect  }from "react";
// import {  fetchById } from './Service';
import { getKeyCLoakAuth} from '../Shared/KeycloakAuth';


function BlockchainListingTable(props) {
  const params=useParams();

    const [show, setShow] = useState(false);
    const [users, setUsers] = useState([]);
    const [loading,setLoading]=useState(true);

    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);

    let history = useHistory();

    const redirect = (id) => {
      localStorage.setItem("blockchainId",id)
      history.push('/Editlistingform')
    }

    // Function to collect data
    const getApiData = async () => {

      const data = await getKeyCLoakAuth().then((data)=> data.json());
      console.log(data);
      var myHeaders = new Headers();
      myHeaders.append("Authorization","Bearer"+" "+data.access_token);
      var requestOptions = {
        method: 'GET',
        headers: myHeaders
      };
      const url = `${process.env.REACT_APP_Integration}/v1/network`;
      const response =  await fetch(url,requestOptions)
      .then((response) => response.json());
        setUsers(response);
        console.log(response);
    };
    
    useEffect(() => {
      getApiData().then(data=>{
        setLoading(false);
        // fetchById(id);
        // setHttpError()
        // setExpenseList(data)
      })
      .catch(error=>{
        setLoading(false)
        // setHttpError(error.message)
      })
    
      // })
    }, []);

    return ( 
        <div> 

{/* Delete Modal code start here */}
    <Modal show={show} onHide={handleClose} aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header>
          <Modal.Title>Alert</Modal.Title>
        </Modal.Header>
        <Modal.Body>Are you sure you want to delete this record!</Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={handleClose}>
            Delete
          </Button>
          <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>

{/* Delete Modal code end here */}


                    <div className="row closetb">
                        <div className="closebtn">
                        <img onClick={props.closerow} src="../images/close-icon.png" title="Close" />
                        {/* <button onClick={props.closerow}>Close</button> */}
                        </div>
                        </div>
                <div className="bottom-space">
                    <div className="table-scroll">
                        <table className="table table-responsive-sm table-hover table-bordered">
                            <thead className="thead">
                                <tr>
                                <th>Network Name</th>

                                    <th>Channel Name</th>
                                    <th>Orgs Count</th>
                                    <th>Peer Count</th>
                                    <th>Orderer Count</th>
                                    {/* <th>Status</th> */}
                                    <th>Action</th>
                                </tr>
                            </thead>
                            <tbody>
                            {users.map((item, i) => (
                                <tr key={i}>
                                     <td>{item.networkName}</td>
                                    <td>{item.channelName}</td>
                                    <td>{item.orgs}</td>
                                    <td>{item.peers}</td>
                                    <td>{item.orderers}</td>
                                    {/* <td>Deploy</td> */}
                                    <td className="icon-inline">
                                        <img onClick={()=> redirect(item.id)} className="edit" src="../images/edit-icon.png" title="Edit" width="26px" height="26px" alt="edit-icon" />
                                        <img onClick={handleShow} className="delete" src="../images/delete-icon.png" title="Delete" width="26px" height="26px" alt="delete-icon" />
                                    </td>
                                </tr>
                                ))}

{/* 
                                <tr>
                                    <td>Hyperledger Fabric</td>
                                    <td>8</td>
                                    <td>4</td>
                                    <td>2</td>
                                    <td><img src="../images/inprogress-icon.png" title="Inprogress" alt="inprogress-icon" /></td>
                                    <td className="icon-inline">
                                        <img className="edit" src="../images/edit-icon.png" title="Edit" width="26px" height="26px" alt="delete-icon" />
                                    </td>
                                </tr> */}

                                {/* <tr>
                                    <td>Poligon</td>
                                    <td>2</td>
                                    <td>7</td>
                                    <td>5</td>
                                    <td><img src="../images/complete-icon.png" title="Complete" alt="complete-icon" /></td>
                                    <td className="icon-inline"> - </td>
                                </tr> */}

                            </tbody>
                        </table>

                    </div>
                </div>
                
</div>

     );
}

export default BlockchainListingTable;