import "../Assets/Styles/Workflow.css";
import Tabs from "react-bootstrap/Tabs";
import Tab from "react-bootstrap/Tab";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import axios from "axios";
import { NavLink } from "react-router-dom";
import React, { useState, useEffect } from "react";
import { Dropdown, Nav} from 'react-bootstrap';
import filtericon from "./../Assets/images/filter-icon.png";
import { Modal, Button } from 'react-bootstrap';

function Workflow(props) {
  const [file, setFile] = useState();
  const [show, setShow] = useState(false);
  const [showp1, setShowp1] = useState(false);

  const [users, setUsers] = useState([]);
  const [detailUsers, setDetailUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [selecteddefination, setSelected] = useState([]);
  const [selecteddefinationbyID, setSelectedBYID] = useState([]);
  const [setdefinationprocessId, setprocessId] = useState([]);
  const [RequestMethod, setRequestMethod] = useState([]);
  const [RequestURL, setRequestURL] = useState([]);
  const [RequestBody, setRequestBody] = useState([]);

  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);
  
  const handleClosep1 = () => setShowp1(false);
  const handleShowp1 = () => setShowp1(true);

  function onFileChange(event) {
    setFile(event.target.files[0]);
        const url = 'https://workflow.lcb1.integrandtech.com/api/process/deployments';
    const formData = new FormData();
    formData.append('file', file);
    axios.post(url, formData).then((response) => {
      console.log(response.data);
    });
  }

   // Function to collect data
   const getDeploymentApiData = async () => {
    const response = await fetch(
      "https://workflow.lcb1.integrandtech.com/api/process/deployments"
    ).then((response) => response.json());
      setUsers(response.data);
  };
  
   // Function to collect data
   const getDefination = async () => {
    const response = await fetch(
      "https://workflow.lcb1.integrandtech.com/api/process/definitions"
    ).then((response) => response.json());
      console.log(response.data);
      setSelected(response.data);
      // setUsers(response.data);
  };

  

  const getDetailApiData = async () => {
    const detailResponse = await fetch(
      "https://workflow.lcb1.integrandtech.com/api/process/definitions"
    ).then((detailResponse) => detailResponse.json());
    // console.log(detailResponse);
    setDetailUsers(detailResponse.data);
  };

  const dropDownValue=(event)=>{
   for(var a =0;a<selecteddefination.length;a++){
      if(selecteddefination[a].resourceName === event.target.value){
        console.log(selecteddefination[a]);
        setprocessId(selecteddefination[a].id)
      }
    }
  }

  const getDefinationById = async () => {
    const url = `${process.env.REACT_APP_Workflow}/api/process/history/instances/definition/${setdefinationprocessId}`;
    console.log(url);
    const definationResponse = await fetch(
      `https://workflow.lcb1.integrandtech.com/api/process/history/instances/definition/${setdefinationprocessId}`
    ).then((definationResponse) => definationResponse.json());
    for(var a =0;a<definationResponse.data;a++){

      if(definationResponse.data.endTime == null){
        definationResponse.data.status = definationResponse.data.activityId
      }else{
        definationResponse.data.status = 'completed' 
      }
    }    
    console.log(definationResponse.data)
    setSelectedBYID(definationResponse.data);
    handleClose();
  };

  const requestMethod=(event)=>{
    console.log(event.target.value);
    setRequestMethod(event.target.value)
  }
  const requestURL=(event)=>{
    console.log(event.target.value);
    setRequestURL(event.target.value)

  }
  const requestBody=(event)=>{
    console.log(event.target.value);
    setRequestBody(event.target.value)

  }

  const invokeButton=async()=>
  {
    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/json");
    const url = 'https://workflow.lcb1.integrandtech.com/api/process/instances';

    // var requestB = {
    //   "dataRecord": {
    //     "childName": "",
    //     "genderCode": "M",
    //     "childWeight": 3.2122,
    //     "permanentAddress": "Parent Address GENX",
    //     "fatherName": "Ajeet",
    //     "motherName": "Mothers Name",
    //     "motherAge": "38"
    // },
    // "channel": {
    //     "name": "demo",
    //     "smartContract": "certificate-test",
    //     "clientUser": "demouser2",
    //     "contractMethod": "createBirthCertificate",
    //     "connectionProfile": "/connection-org1.yaml"
    // },
    // "blockChain": "HyperledgerFabric"
    // }

    var payload = JSON.stringify({
      
        "processDefinitionId": "process1:1:b5bf9145-1d6b-11ed-ac93-86d5e28e3daa",
        "userId": "admin",
        "variables": {
            "requestMethod": RequestMethod,
            "requestUrl": RequestURL,
            "requestBody": RequestBody,
    }
    })
    var requestOptions = {
      method:'POST',
      headers: myHeaders,
      body: payload,
      redirect: 'follow'
    };
    fetch("https://workflow.lcb1.integrandtech.com/api/process/instances", requestOptions)
    .then(response => response.text())
    .then(result => console.log(result))
    .catch(error => console.log('error', error));
  }

  useEffect(() => {
    getDetailApiData().then(data=>{setLoading(false);})
    .catch(error=>{setLoading(false)})

    getDeploymentApiData().then(data=>{setLoading(false);})
    .catch(error=>{setLoading(false)})
    getDefination().then(data=>{setLoading(false);})
    .catch(error=>{setLoading(false)})
    // getDefinationById().then(data=>{setLoading(false);})
    // .catch(error=>{setLoading(false)})
  }, []);

  return (
    <div className="content">
      <nav className="navbar navbar-expand-lg navbar-light bg-light contentcolor">
        <div className="container-fluid">
          <div className="heading">
            <h4>WorkFlow</h4>
          </div>
        </div>
      </nav>

      <div className="row1">
        <Tabs defaultActiveKey="first">
           <Tab eventKey="first" title="Create Deployment" className="resourcetab">
           
            <Col md={6}>
              <Row>
                <div md={4}>
                  <label className = "xml-upload">XML file</label>
                </div>
                <div md={8}>
                  <input
                    type="file"
                    className="codefilealign1"
                    onChange={onFileChange}
                    placeholder="Network Name"
                  />
                </div>
              </Row>
            </Col>
            <div className="bottom-space">
              <div className="table-scroll">
                <table className="table table-responsive-sm table-hover table-bordered">
                  <thead className="thead">
                    <tr>
                      <th>Name</th>
                      <th>Key</th>
                      {/* <th>Network Name</th> */}
                      {/* <th>Channel Name</th> */}
                      <th>Deployment Time</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {users.map((item, i) => (
                      <tr key={i}>
                        <td>{item.name}</td>
                        <td>{item.key}</td>
                        {/* <td>{item.networkName}</td>
                        <td>{item.channelName}</td> */}
                        <td>{item.deploymentTime}</td>
                        <td className="icon-inline">
                          <img
                            className="edit"
                            src="../images/edit-icon.png"
                            title="Edit"
                            width="26px"
                            height="26px"
                            alt="edit-icon"
                          />
                          <img
                            className="delete"
                            src="../images/delete-icon.png"
                            title="Delete"
                            width="26px"
                            height="26px"
                            alt="delete-icon"
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </Tab>
          {/* 3graph code end here*/}

          

          <Tab eventKey="second" title="History">
          <div class="add-icon" >
        <button  onClick={handleShow} id="filter-modal" type="button">
            <span class="filter-icon"><img src={filtericon} width="24px" height="24px" alt="Filter-icon" /></span><span class="btn-filter">Filter</span>
        </button>
        </div>
          <div md={8}>
          
                  {/* <input placeholder="Network Name" onChange={getDefination}/> */}
                </div>
            <div className="bottom-space">
              <div className="table-scroll">
                <table className="table table-responsive-sm table-hover table-bordered">
                  <thead className="thead">
                    <tr>
                      <th>Name</th>
                      <th>Key</th>
                      <th>Status</th>
                      <th>Time</th>
                      <th>Action</th>
                    </tr>
                  </thead>
                  <tbody>
                    {selecteddefinationbyID.map((item, i) => (
                      item.endTime === null ? (
                      <tr key={i}>
                        <td>{item.processDefinitionName}</td>
                        <td>{item.processDefinitionKey}</td>
                        <td >{item.activityId}</td>
                        <td>{item.startTime}</td>

                        <td className="icon-inline">
                      {/* <button className="test-btn" title="Test" onClick={handleShowp1} id="filter-modal" type="button">
            <span   class="btn-filter">Invoke</span>
        </button> */}
                          <img
                            className="edit"
                            src="../images/test-icon.png"
                            title="invoke"
                            width="24px"
                            height="24px"
                            alt="edit-icon"
                            onClick={handleShowp1}
                          />
                          <img
                            className="edit"
                            src="../images/edit-icon.png"
                            title="Edit"
                            width="26px"
                            height="26px"
                            alt="edit-icon"
                          />
                          <img
                            className="delete"
                            src="../images/delete-icon.png"
                            title="Delete"
                            width="26px"
                            height="26px"
                            alt="delete-icon"
                          />
                        </td>
                      </tr>
                      ):
                      <tr key={i}>
                        <td>{item.processDefinitionName}</td>
                        <td>{item.processDefinitionKey}</td>
                        <td >Completed</td>
                        <td>{item.startTime}</td>

                        <td className="icon-inline">
                        <img
                            className="edit"
                            src="../images/test-icon.png"
                            title="invoke"
                            width="24px"
                            height="24px"
                            alt="edit-icon"
                            onClick={handleShowp1}
                          />
                          <img
                            className="edit"
                            src="../images/edit-icon.png"
                            title="Edit"
                            width="26px"
                            height="26px"
                            alt="edit-icon"
                          />
                          <img
                            className="delete"
                            src="../images/delete-icon.png"
                            title="Delete"
                            width="26px"
                            height="26px"
                            alt="delete-icon"
                          />
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </div>
          </Tab>

          
        </Tabs>
      </div>

      {/* Filter Modal code start here */}
<Modal show={show} onHide={handleClose} aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header>
          <Modal.Title>Filter</Modal.Title>
        </Modal.Header>
        <Modal.Body>
       
      <Row className='filter-row'>
        <Col sm={5}>
          <label className='filter-label'>WorkFlow Name</label>
          </Col>
        <Col sm={7}>
          {/* <h1>{selecteddefinationbyID}</h1> */}
        <select className='method-select' onChange={dropDownValue}>
        <option >Select Workflow </option>
        {selecteddefination.map((item,indx)=>{
          return (
          <option key={indx}>{item.resourceName}</option>
          )})
        }
        </select>
        </Col>
      </Row>
      {/* <Row className='filter-row'>
        <Col sm={5}><label className='filter-label'>Channel Name</label></Col>
        <Col sm={7}><input className='filter-input' placeholder='Enter channel name'/></Col>
      </Row> */}
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={getDefinationById}>
            Apply Filter
          </Button>
          <Button variant="secondary" onClick={handleClose}>
            Reset
          </Button>
        </Modal.Footer>
      </Modal>

      

{/* Filter Modal code end here */}

<Modal show={showp1} onHide={handleClosep1} aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header>
          <Modal.Title>Invoke</Modal.Title>
        </Modal.Header>
        <Modal.Body>
       
      <Row className='filter-row'>
        <Col sm={5}>
          <label className='filter-label'>Request Method</label>
          </Col>
        <Col sm={7}><input className='filter-input' onChange={requestMethod} placeholder='Enter channel name'/>
        </Col>
      </Row>
      <Row className='filter-row'>
        <Col sm={5}><label className='filter-label'>Request URL</label></Col>
        <Col sm={7}><input className='filter-input' onChange={requestURL} placeholder='Enter channel name'/></Col>
      </Row>
      <Row className='filter-row'>
        <Col sm={5}><label className='filter-label'>Request Body</label></Col>
        <Col sm={7}>
          {/* <input className='filter-input' onChange={requestBody} placeholder='Enter channel name'/> */}
          <textarea className="mediainput1" onChange={requestBody} rows="50" cols="50"></textarea>

          </Col>
      </Row>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="danger" onClick={invokeButton}>
            Invoke
          </Button>
          <Button variant="secondary" onClick={handleClosep1}>
            Reset
          </Button>
        </Modal.Footer>
      </Modal>

    </div>
      );
}

export default Workflow;
