import '../Assets/Styles/EditIntegrationform.css';
import Card from "react-bootstrap/Card";
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import { useHistory } from 'react-router-dom';
import React from "react";
import Tabs from 'react-bootstrap/Tabs';
import Tab from 'react-bootstrap/Tab';

function EditIntegrationform() {
    let history = useHistory();

    const redirect = (e) => {
      history.goBack();
      e.preventDefault();
    }
    
    return ( 
        <div className="content">
            <nav className="navbar navbar-expand-lg navbar-light bg-light contentcolor">
                <div className="container-fluid">
                    <div className="heading">
                        <h4>Integration Tool</h4>
                    </div>
                </div>
            </nav>

            <div className="row">
                    <div className="breadcrumb">
                    <ul>
                    <li>Integration Tool/ Integration Tool Listing/ <b>Edit Form</b></li>
                    </ul> 
                    </div>
            </div>

            <div className="row1">
                <Card className="card-form" >
                    <Card.Body>
                        <div className="heading-icon">
                            <h4><span><img src="../images/edit-form-icon.png"/></span>Edit Form</h4>
                        </div>

                        <div className="row1">
                        <Tabs defaultActiveKey="first">
                        <Tab eventKey="first" title="Headers" >
                          <Row className="row-bottom-top formedit1">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Type</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter type" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                        </Tab>
                        

                        <Tab eventKey="second" title="QueryParams" className="resourcetab" >
                        <Row className="row-bottom-top formedit1">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Type</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter type" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                        </Tab>

                        <Tab eventKey="third" title="PathParams" className="resourcetab" >
                        <Row className="row-bottom-top formedit1">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Type</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network input-edit" placeholder="Enter type" />
                                        </div>
                                    </Row>
                                </Col>
                        </Row>
                        </Tab>

                        <Tab eventKey="forth" title="Request" className="resourcetab" >
                        <Row className="row-bottom-top formedit1">
                                <Col md={12}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Media Type</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network inputmedia-2" placeholder="Enter media type" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                            <Row className="row-bottom-top formedit1">
                            <Col md={12}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Body</label>
                                        </div>
                                        <div md={8}>
                                        <textarea className="textarea-wdt-1" rows="65" cols="65"></textarea>
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                        </Tab>

                        <Tab eventKey="fifth" title="Response" className="resourcetab" >
                        <Row className="row-bottom-top formedit1">
                                <Col md={12}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Media Type</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network inputmedia-2" placeholder="Enter media type" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                            <Row className="row-bottom-top formedit1">
                            <Col md={12}>
                                    <Row>
                                        <div md={4}>
                                            <label className="label-name">Body</label>
                                        </div>
                                        <div md={8}>
                                        <textarea className="textarea-wdt-1" rows="65" cols="65"></textarea>
                                        </div>
                                    </Row>
                                </Col>
                            </Row>

                            <Row>
                                <div className="btn-btm">
                                <input onClick= {redirect} type="button" className="nav-save" value="Back" />
                                <input type="button" className="sub-button" value="Submit" />
                                <input type="button" className="sub-button" value="Cancel" />
                                </div>
                            </Row>
                        </Tab>

                        </Tabs>
                        </div>
                    </Card.Body>
                </Card>
            </div>

        </div>
     );
}

export default EditIntegrationform;
