import '../Assets/Styles/BlockchainNetwork.css';
// import { useState } from "react";
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import BlockchainListingTable from '../Components/BlockchainListingTable';
// import { ListGroup } from 'reactstrap';
// import {Modal} from 'react-bootstrap';
import React, { useState,useEffect  } from "react";
import { getKeyCLoakAuth} from '../Shared/KeycloakAuth';
import { Modal, Button } from 'react-bootstrap';
import { NavLink} from 'react-router-dom';

function BlockchainNetwork(props) {
    const [visible,setVisible]=useState(false);

    const listrow=()=>{setVisible(true)};
    const closerow=()=>{setVisible(false)};
    const [networkName, setNetworkName] = useState('');
    const [channelName, setChannelName] = useState('');
    const [orgCount, setOrgCount] = useState('');
    const [ordererCount, setOrdererCount] = useState('');
    const [peerCount, setPeerCount] = useState('');
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    const [show, setShow] = useState(false);

    const NetworkName=(event)=>{
        event.preventDefault(); // 👈️ prevent page refresh
        setNetworkName(event.target.value)
    }
    const ChannelName=(event)=>{
        event.preventDefault(); // 👈️ prevent page refresh
        setChannelName(event.target.value)
    }
    const OrgCount=(event)=>{
        event.preventDefault(); // 👈️ prevent page refresh
        setOrgCount(event.target.value)
    }
    const OrdererCount=(event)=>{
        event.preventDefault(); // 👈️ prevent page refresh
        setOrdererCount(event.target.value)
    }
    const PeerCount=(event)=>{
        event.preventDefault(); // 👈️ prevent page refresh
        setPeerCount(event.target.value)
    }

    const saveButton = async (event) => {
        var raw = JSON.stringify({
        "networkName":networkName,
        "blockchainType":"Hyperledger",
        "peers":peerCount,
        "orgs":orgCount,
        "orderers":ordererCount,
        "channelName":channelName
    });
        const data = await getKeyCLoakAuth().then((data)=> data.json())
            var myHeaders = new Headers();
            myHeaders.append("Authorization","Bearer"+" "+data.access_token);
            myHeaders.append("Content-Type", "application/json");
            var requestOptions = {
              method: 'POST',
              headers: myHeaders,
            //   body:JSON.stringify(payload)
            body:raw
            };
            const url = `${process.env.REACT_APP_Integration}/v1/network`;
            const response =  await fetch(url,requestOptions)
            .then((response) => response.json());
            console.log(response);
            handleShow();
            //   setUsers(response);
        };

        
        // useEffect(() => {
        //     saveButton().then(data=>{
        //     setLoading(false);
        //     // setHttpError()
        //     // setExpenseList(data)
        //   })
        //   .catch(error=>{
        //     setLoading(false)
        //     // setHttpError(error.message)
        //   })
        
        //   // })
        // }, []);

        return (
            <div className="content">
                <nav className="navbar navbar-expand-lg navbar-light bg-light contentcolor">
                    <div className="container-fluid">

                        <div className="heading">
                            <h4>Blockchain Networks</h4>
                        </div>
                    </div>
                </nav>

                <div className="content1">
                <div className="row1">
                <Card className="card-network">
                <Card.Body>
                <div className="heading-icon">
                    <h4 className='hyper-title'><span><img src="../images/hyperledger-icon.png" width="35px" height="35px"/></span>Hyperledger Fabric</h4>
                </div>
                <form className='formNetwork' >
                <p><b>Network Name:  <span className="space2">
                <input className='nw-input' onChange={NetworkName} placeholder='Network Name' /></span></b></p>
                <h4>Channel Name:     <span className="space1"><input className='nw-input1' onChange={ChannelName} placeholder='channel Name'/></span></h4>
                <h4>Orgs Count:     <span className="space1"><input className='nw-input' style={{marginLeft:"12%"}} onChange={OrgCount} placeholder='Orgs Count'/></span></h4>
                <h4 >Peer Count:     <span className="space2"><img src="../images/info-icon.png" width="12px" height="12px" title="Peers count per organisation" /><input className='nw-input' style={{marginLeft:"10%"}} onChange={PeerCount} placeholder='Peers Count'/></span></h4>
                <h4>Orderers Count:     <span className="space1"><input className='nw-input1' onChange={OrdererCount} placeholder='Orderers Count'/></span></h4>
                </form>
                </Card.Body>
                <Card.Footer>
                    <div className='row'>
                        <div className='listing-footer'>
                        <button className="nav-button-list" onClick={listrow}>Listing</button>
                        <button className="nav-button-saves" onClick={saveButton}>Save</button>
                        </div>
                    </div>
                </Card.Footer>
                </Card>
                </div>
                </div>
            {visible && <BlockchainListingTable closerow={closerow}/> }

    <Modal show={show} onHide={handleClose} aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header>
          <Modal.Title>Alert</Modal.Title>
        </Modal.Header>
        <Modal.Body>Record Added Successfully</Modal.Body>
        <Modal.Footer>
        <NavLink className="menu-item" activeClassName="menubg" to={'/blockchainnetwork'} exact >
        <a className="nav-link" href="#">
          <Button variant="danger" onClick={handleClose}>
            Ok 
          </Button>
          </a>
        </NavLink>
        <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>
            </div>

        );
    }

export default BlockchainNetwork;