import '../Assets/Styles/ManageSmartContract.css';
import Card from "react-bootstrap/Card";
import Col from "react-bootstrap/Col";
import Row from "react-bootstrap/Row";
import { useHistory } from 'react-router-dom';
import React, { useState ,Component} from "react";
import { Modal, Button } from 'react-bootstrap';
import axios from "axios";
import { getKeyCLoakAuth} from '../Shared/KeycloakAuth';
import { NavLink} from 'react-router-dom';


function ManageSmartContract() {
    let history = useHistory();

    const [show, setShow] = useState(false);
    const handleClose = () => setShow(false);
    const handleShow = () => setShow(true);
    
    const [file, setFile] = useState()
    const [smartContractName, setsmartContractName] = useState()
    const [networkName, setnetworkName] = useState()
    const [channelName, setchannelName] = useState()

    const redirect = (e) => {
      history.goBack();
      e.preventDefault();
    }
    
    function onFileChange(event) {
      setFile(event.target.files[0])
    }

    const smartContractNameClick = (event)=>{
        setsmartContractName(event.target.value)
    }
    const networkNameClick = (event)=>{
        setnetworkName(event.target.value)
    }
    const channelNameClick = (event)=>{
        setchannelName(event.target.value)
    }

    const handleSubmit = async () => {
    //   event.preventDefault()
      const data = await getKeyCLoakAuth().then((data)=> data.json())
            var myHeaders = new Headers();
            myHeaders.append("Authorization","Bearer"+" "+data.access_token);
      var payload = { 
              "name": smartContractName, 
              "networkName": networkName, 
              "channelName": channelName
          } 
      const formData = new FormData();
      formData.append('codeUploaded', file);
      formData.append('data', JSON.stringify(payload));
      var requestOptions = {
        method: 'POST',
        headers: myHeaders,
        body:formData
  };
  
  const url = `${process.env.REACT_APP_Integration}/v1/smartcontract`;

  const response =  await fetch(url,requestOptions)
            .then((response) => response.json());
    handleShow();
    }

    return ( 
        <div className="content">
            <div className="row1">
                <Card className="card-form" >
                    <Card.Body>
                        <div className="heading-icon">
                            <h4><span><img src="../images/manage-icon.png"/></span>Manage Smart Contract</h4>
                        </div>

                        <form className='formedit'>
                            <Row className="row-bottom-top">
                                <Col md={6}>
                                <Row>
                                        <div md={4}>
                                            <label>Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network wdt" onChange={smartContractNameClick} placeholder="Enter name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                <Row>
                                    <div md={4}>
                                        <label>Code Upload</label>
                                    </div>
                                        <div md={8}>
                                        <input type="file" className="codefilealign" onChange={onFileChange} placeholder="Network Name" />
                                        {/* <textarea className="textarea-wdt" rows="31" cols="31"></textarea> */}
                                        </div>
                                    </Row>                                    
                                </Col>
                            </Row>

                            <Row className="row-bottom">
                                <Col md={6}>
                                    <Row>
                                        <div md={4}>
                                            <label>Network Name</label>
                                        </div>
                                        <div md={8}>
                                           <input type="text" className="network wdt2" onChange={networkNameClick} placeholder="Enter blockchain network name" />
                                        </div>
                                    </Row>
                                </Col>

                                <Col md={6}>
                                <Row>
                                        <div md={4}>
                                            <label>Channel Name</label>
                                        </div>
                                        <div md={8}>
                                            <input type="text" className="network wdt3" onChange={channelNameClick} placeholder="Enter channel name" />
                                        </div>
                                    </Row>
                                </Col>
                            </Row>
                            <Row>
                                <div className="btn-btm">
                                    <input onClick= {redirect} type="button" className="nav-save" value="Back" />
                                    <input onClick={handleSubmit} type="button" className="sub-button" value="Submit" />
                                   <input type="button" className="sub-button" value="Cancel" />
                                </div>
                            </Row>
                        </form>
                    </Card.Body>
                </Card>
            </div>

{/* Delete Modal code start here */}
<Modal show={show} onHide={handleClose} aria-labelledby="contained-modal-title-vcenter"
      centered>
        <Modal.Header>
          <Modal.Title>Alert</Modal.Title>
        </Modal.Header>
        <Modal.Body>Record Added Successfully</Modal.Body>
        <Modal.Footer>
        <NavLink className="menu-item" activeClassName="menubg" to={'/smartcontract'} exact >
        <a className="nav-link" href="#">
          <Button variant="danger" onClick={handleClose}>
            Ok 
          </Button>
          </a>
        </NavLink>
        <Button variant="secondary" onClick={handleClose}>
            Cancel
          </Button>
        </Modal.Footer>
      </Modal>

{/* Delete Modal code end here */}  


        </div>

     );
}

export default ManageSmartContract;