  const [users1, setUsers1] = useState([]);
console.log(localStorage.getItem("role"));
  const smartcontractAPI = async () => {

    const data = await getKeyCLoakAuth().then((data)=> data.json());
    var myHeaders = new Headers();
    myHeaders.append("Authorization","Bearer"+" "+data.access_token);
    var requestOptions = {
      method: 'GET',
      headers: myHeaders
    };
    const url = `${process.env.REACT_APP_Integration}/v1/smartcontract`;
    const response =  await fetch(url,requestOptions)
        .then((response) => response.json());
          setUsers(response.length);
 
  };

  const getApiData = async () => {
    const data = await getKeyCLoakAuth().then((data)=> data.json())
        var myHeaders = new Headers();
        myHeaders.append("Authorization","Bearer"+" "+data.access_token);
        var requestOptions = {
          method: 'GET',
          headers: myHeaders
        };
        const url = `${process.env.REACT_APP_Integration}/v1/network`;
        const response =  await fetch(url,requestOptions)
    .then((response) => response.json());
    setUsers1(response.length);
    };

  useEffect(() => {
    getApiData().then(data=>{setLoading(false); })
    .catch(error=>{setLoading(false)    })
    smartcontractAPI().then(data=>{setLoading(false); })
    .catch(error=>{setLoading(false)    })
    // })
  }, []);

